import { _decorator, Component, EventTouch, Node, tween } from 'cc';
import { MatchChecker } from '../Grid/MatchChecker';
const { ccclass , property } = _decorator;

@ccclass('InteractionManager')
export class InteractionManager extends Component {
    private grid: Node[][] = [];
    // private selectedPiece: Node | null = null;
    private firstSelectedPiece : Node | null = null;
    private secondSelectedPiece : Node | null = null;


    public initializeGrid(grid: Node[][]) {
        this.grid = grid;
    }

    public onPieceClicked(event: EventTouch, piece: Node) {
        if (this.firstSelectedPiece) {
            this.secondSelectedPiece = piece;
            const pieceA = this.firstSelectedPiece;
            const pieceB = this.secondSelectedPiece;

            console.log(pieceA,pieceB);
            

            const posA = pieceA.getPosition();
            const posB = pieceB.getPosition();

            // Swap positions
            pieceA.setPosition(posB);
            pieceB.setPosition(posA);

            const rowA = this.findPieceRow(pieceA);
            const colA = this.findPieceCol(pieceA);
            const rowB = this.findPieceRow(pieceB);
            const colB = this.findPieceCol(pieceB);

            if (!MatchChecker.checkMatch(this.grid, pieceA, rowA, colA) && !MatchChecker.checkMatch(this.grid, pieceB, rowB, colB)) {
                // Swap back if no match
                tween(pieceA).to(0.3, { position: posA }).start();
                tween(pieceB).to(0.3, { position: posB }).start();
            }

            
            this.resetSelection();
        } else {
            this.firstSelectedPiece = piece;
            this.highlightPiece(piece);
        }
    }

    private findPieceRow(piece: Node): number {
        // Implement logic to find the row of the piece in the grid.
        // Example: return this.grid.findIndex(row => row.includes(piece));
        return -1; // Placeholder, implement this logic.
    }

    private findPieceCol(piece: Node): number {
        // Implement logic to find the column of the piece in the grid.
        // Example: return this.grid[row].indexOf(piece);
        return -1; // Placeholder, implement this logic.
    }

    private highlightPiece(piece: Node) {
        piece.setScale(1.2, 1.2, 1.2);
    }

    private resetSelection(){
        this.firstSelectedPiece!.setScale(1,1,1)
        this.secondSelectedPiece!.setScale(1,1,1)
        this.firstSelectedPiece = null;
        this.secondSelectedPiece = null;
    }

}
